<template>
	<view class="page">
		<view class="name" v-if="name == 'real_name'">
			<view class="left">
				姓名
			</view>
			<view class="right">
				<input type="text" placeholder="请输入真实姓名" class="input" v-model="value"  maxlength='20' placeholder-class="input_p"/>
			</view>
		</view>
		<view class="nickName" v-if="name == 'name'">
			<view class="name" >
				<view class="left">
					昵称
				</view>
				<view class="right2">
					<input type="text"  placeholder="请输入昵称" class="input" v-model="value" maxlength='20' placeholder-class="input_p"/>
					<text class="input_p">{{value ? value.length : 0}}/20</text>
				</view>
			</view>
			<view class="tip">
				设置后，其他人将看到你的昵称
			</view>
		</view>
		<view class="photo" @click="setPhoto()" v-if="name == 'img'">
			<image :src="regexImg(value)"   v-if="value" mode="aspectFill"></image>
			<image src="../static/img/user.png"   v-else mode="aspectFill"></image>
		</view>
		<view class="bottom_box">
			<button  class="btn" @click="onSave()">保存</button>
		</view>
	</view>
</template>

<script>
	import {
		mapState,mapMutations
	} from 'vuex'
	export default {
		data(){
			return{
				name:'',
				value:'',
				loading:false
			}
		},
		computed: {
			...mapState(['USER_INFO','TEMPORARY_USER'])
		},
		methods:{
			...mapMutations(['SET_USER_INFO','SET_TEMPORARY_USER']),
			onSave(){
				if(this.name == 'real_name'){
					if (!this.$regular.funShowToast('name', this.value , '请输入真实姓名！')) return false 
				}
				if(this.name == 'name'){
					if (!this.$regular.funShowToast('nickName', this.value , '请输入昵称！')) return false 
				}
				if(this.name == 'img'){
					if (!this.$regular.funShowToast(null, this.value , '请上传头像！')) return false 
				}
				var params = this.TEMPORARY_USER
				params[this.name] = this.value
				this.SET_TEMPORARY_USER(params)
				uni.navigateBack()
			},
			setPhoto() {
				uni.chooseImage({
					count: 1, 
					sizeType: ['original', 'compressed'],
					sourceType: ['album','camera'],
					success: (res) => {
						this.$upload(res.tempFilePaths[0]).then(result =>{
							if(result.code == 0) this.value =   result.data[0]
						})
					}
				})
			}
		},
		onLoad(params){
			this.name = params.name
			this.value = params.value  && params.value != 'null' ? decodeURIComponent(params.value)  : ''
		}
	}
</script>

